import clsx from 'clsx'
import React, { FC } from 'react'
import { Typography } from '../../other/Typography'
import { LazyImage } from '../../other/LazyImage'
import { kztAmount } from '../../../utils/money'
import { Condition } from '../../other/Condition'
import { BriefCardProps } from './types'
import './BriefCard.scss'

const BriefCard: FC<BriefCardProps> = ({
  isOutOfStock = false,
  price = 0,
  loading = 'lazy',
  title = '',
  outOfStockTitle = '',
  className,
  imgLqip = 'images/productCardThumbnail.png',
  imgSrc = '',
  imgAlt = '',
}) => {
  const priceTemplate = price ? kztAmount(Number(price)) : ''
  const cardText = isOutOfStock ? outOfStockTitle : priceTemplate
  return (
    <div className={clsx(className, 'BriefCard')}>
      <div className='BriefCard__ImgWrapper'>
        <LazyImage
          alt={imgAlt}
          className='BriefCard__Img'
          loading={loading}
          lqip={imgLqip}
          src={imgSrc}
          title={title}
        />
        <div className='BriefCard__Color' />
      </div>

      <Condition match={isOutOfStock || price}>
        <div className='BriefCard__TitleWrapper'>
          <Typography
            className={clsx('BriefCard__Title', isOutOfStock && '--outOfStock')}
            text={cardText}
            type='smallBodyBold'
          />
        </div>
      </Condition>
    </div>
  )
}

export default BriefCard
